import serverUrl from '../../../common/serveUrl.js'
export default {
	data() {
		return {
			targetIndex:0,
			tabbarIndex: 0,
			headerPosition:"fixed",
			headerTop:"0px",
			typeText:{
				unpaid:'等待付款',
				back:'等待商家发货',
				unreceived:'商家已发货',
				received:'等待用户评价',
				completed:'交易已完成',
				refunds:'商品退货处理中',
				cancelled:'订单已取消'
			},
			orderType: ['全部','待付款','待发货','待收货'],
			
			list:[],
			tepmPay:[],
			tabbarIndex:0
		};
	},
	onLoad(option) {
		//option为object类型，会序列化上个页面传递的参数
		console.log("option: " + JSON.stringify(option));
		let tbIndex = parseInt(option.tbIndex)+1;
		this.tabbarIndex = tbIndex;
		//兼容H5下排序栏位置









		
		this.loading()
		
	},
	onPageScroll(e){
		return;
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
	},
	methods: {
		loading(){
			var that = this;
			var url =serverUrl.serverUrl
			console.log(url)
			uni.request({
				url: url+"/app/order/myOrder?status=1&userId=1",
				method:"POST",
				success(res) {
					var infon = res.data.data.data
					that.list  = infon
				}
			})
		},
		showType(index){
			var that = this;
			var url =serverUrl.serverUrl
			this.targetIndex =index;
			var status = index+1
			// console.log(status)
			uni.request({
				url:url+"/app/order/myOrder?status="+status+"&userId=1",
				method:"POST",
				success(res) {
					var tepmPay = res.data.data.data;
					that.list = tepmPay;
				}
			})
			
		},
		showLogistics(row){
			
		},
		remindDeliver(row){
			uni.showToast({
				title:'已提醒商家发货'
			})
		},
		cancelOrder(row){
			
			var id = row.id
			var that = this;
			var url=serverUrl.serverUrl;
			uni.showModal({
				title: '取消订单',
				content: '确定取消此订单？',
				success: (res)=>{
					if (res.confirm) {
						uni.request({
							url:url+"/app/order/cancelOrder?id="+id,
							method:"POST",
							success() {
								that.loading()
							}
							
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
			
			
			
			
		
		},
		doCancelOrder(ordersn){
			let typeNum = this.orderList.length;
			for(let i=0;i<typeNum;i++){
				let list = this.orderList[i];
				let orderNum = list.length;
				if(orderNum>0 && list[0].type=='unpaid'){
					for(let j=0;j<orderNum;j++){
						if(this.orderList[i][j].ordersn == ordersn){
							this.orderList[i][j].type = 'cancelled';
							break;
						}
					}
				}
				
			}
		},
		toPayment(row){
			//本地模拟订单提交UI效果
			uni.showLoading({
				title:'正在获取订单...'
			})
			let paymentOrder = [];
			paymentOrder.push(row);
			setTimeout(()=>{
				uni.setStorage({
					key:'paymentOrder',
					data:paymentOrder,
					success: () => {
						uni.hideLoading();
						uni.navigateTo({
							// url:'../../pay/payment/payment?amount='+row.payment
							url:"./pay"
						})
					}
				})
			},500)
		}
	}
}
